import { Component, OnInit } from '@angular/core';

const arrWords = [
  { id: 1, en: 'action', vn: 'hành động', memorized: true },
  { id: 2, en: 'actor', vn: 'diễn viên', memorized: false },
  { id: 3, en: 'activity', vn: 'hoạt động', memorized: true },
  { id: 4, en: 'active', vn: 'chủ động', memorized: true },
  { id: 5, en: 'bath', vn: 'tắm', memorized: false },
  { id: 6, en: 'bedroom', vn: 'phòng ngủ', memorized: true }
];

@Component({
  selector: 'app-struct',
  templateUrl: './struct.component.html',
  styleUrls: ['./struct.component.css']
})
export class StructComponent implements OnInit {
  filterStatus = 'Remem';
  array2 = arrWords;
  isShow = false;
  newVn = '';
  newEn = '';
  array = [{ name: 'huy', id: 1 }, { name: 'huy2', id: 2 }, { name: 'huy2', id: 2 }];
  constructor() { }

  ngOnInit() {
  }
  addWord() {
    if (this.isShow == true) {
      this.array2.unshift({
        id: this.array2.length + 1,
        en: this.newEn,
        vn: this.newVn,
        memorized: false
      });
      this.newEn = '';
      this.newVn = '';
    }
    this.isShow = !this.isShow;
  }
  popArr(id: number) {
    const index = this.array2.findIndex(word => word.id === id);
    this.array2.splice(index, 1);
  }
  getShowStatus(memorized: boolean){
    const all = this.filterStatus == 'All';
    const remem = this.filterStatus == 'Remem' && memorized;
    const notRemem = this.filterStatus == 'NotRemem' && !memorized;
    return all || remem || notRemem; 
  }
  
}
